"use client";

import { useEffect, useState } from "react";
import api from "@/lib/axios";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Users, CreditCard, ShoppingCart, Activity, TrendingUp, DollarSign } from "lucide-react";
import { Avatar, AvatarFallback } from "@/components/ui/avatar";
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

interface Order {
    id: number;
    reference: string;
    cost: number;
    status: string;
    user?: {
        email: string;
        name?: string;
    };
    created_at: string;
}

interface AnalyticsStats {
    total_revenue: number;
    total_orders: number;
    pending_deposits: number;
    pending_orders: number;
    total_users: number;
    recent_orders: Order[];
    monthly_revenue: { month: string; total: number }[];
    order_status: { status: string; count: number }[];
}

export default function AdminDashboardPage() {
    const [stats, setStats] = useState<AnalyticsStats | null>(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        api.get('/admin/analytics')
            .then(res => setStats(res.data))
            .catch(err => console.error("Failed to fetch stats", err))
            .finally(() => setLoading(false));
    }, []);

    if (loading) {
        return (
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4 animate-pulse">
                {[...Array(4)].map((_, i) => (
                    <div key={i} className="h-32 bg-slate-200 dark:bg-slate-800 rounded-lg"></div>
                ))}
            </div>
        );
    }

    if (!stats) return <div className="text-red-500">Failed to load analytics.</div>;

    const cards = [
        {
            title: "Total Revenue",
            value: `GHS ${stats.total_revenue.toLocaleString()}`,
            icon: DollarSign,
            color: "text-green-500",
            bg: "bg-green-100 dark:bg-green-500/10",
            desc: "+12% from last month"
        },
        {
            title: "Total Orders",
            value: stats.total_orders,
            icon: ShoppingCart,
            color: "text-blue-500",
            bg: "bg-blue-100 dark:bg-blue-500/10",
            desc: "New orders received"
        },
        {
            title: "Pending Orders",
            value: stats.pending_orders,
            icon: Activity,
            color: "text-orange-500",
            bg: "bg-orange-100 dark:bg-orange-500/10",
            desc: "Requires attention"
        },
        {
            title: "Total Users",
            value: stats.total_users,
            icon: Users,
            color: "text-purple-500",
            bg: "bg-purple-100 dark:bg-purple-500/10",
            desc: "Active customers"
        },
    ];

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <h2 className="text-3xl font-bold tracking-tight text-slate-800 dark:text-slate-100">Dashboard Overview</h2>
            </div>

            <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-4">
                {cards.map((card) => (
                    <Card key={card.title} className="text-card-foreground shadow-sm border-none shadow-[0_2px_10px_0_rgba(67,89,113,0.12)] dark:shadow-none dark:bg-slate-800">
                        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                            <CardTitle className="text-sm font-medium text-muted-foreground">
                                {card.title}
                            </CardTitle>
                            <div className={`p-2 rounded-lg ${card.bg}`}>
                                <card.icon className={`h-4 w-4 ${card.color}`} />
                            </div>
                        </CardHeader>
                        <CardContent>
                            <div className="text-2xl font-bold">{card.value}</div>
                            <p className="text-xs text-muted-foreground mt-1">
                                {card.desc}
                            </p>
                        </CardContent>
                    </Card>
                ))}
            </div>

            <div className="grid gap-6 md:grid-cols-1 lg:grid-cols-2">
                <Card className="col-span-1 shadow-[0_2px_10px_0_rgba(67,89,113,0.12)] border-none dark:bg-slate-800">
                    <CardHeader>
                        <CardTitle>Recent Orders</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="space-y-6">
                            {stats.recent_orders.length === 0 ? (
                                <p className="text-sm text-muted-foreground">No recent orders found.</p>
                            ) : (
                                stats.recent_orders.map((order) => (
                                    <div key={order.id} className="flex items-center justify-between">
                                        <div className="flex items-center gap-4">
                                            <Avatar className="h-9 w-9">
                                                <AvatarFallback className="bg-primary/10 text-primary">
                                                    {order.user?.email?.substring(0, 2).toUpperCase() || 'U'}
                                                </AvatarFallback>
                                            </Avatar>
                                            <div className="space-y-1">
                                                <p className="text-sm font-medium leading-none">{order.reference}</p>
                                                <p className="text-sm text-muted-foreground">
                                                    {order.user?.email || 'Unknown User'}
                                                </p>
                                            </div>
                                        </div>
                                        <div className="flex flex-col items-end gap-1">
                                            <div className="font-semibold text-sm">
                                                +GHS {order.cost}
                                            </div>
                                            <span className={`text-[10px] px-2 py-0.5 rounded-full font-medium capitalize
                                                ${order.status === 'completed' ? 'bg-green-100 text-green-600 dark:bg-green-900/30' :
                                                    order.status === 'pending' ? 'bg-orange-100 text-orange-600 dark:bg-orange-900/30' :
                                                        'bg-slate-100 text-slate-600 dark:bg-slate-800'}
                                            `}>
                                                {order.status || 'Unknown'}
                                            </span>
                                        </div>
                                    </div>
                                ))
                            )}
                        </div>
                    </CardContent>
                </Card>

                {/* Placeholder for Revenue Chart or additional widget */}
                <Card className="col-span-1 shadow-[0_2px_10px_0_rgba(67,89,113,0.12)] border-none bg-primary text-primary-foreground relative overflow-hidden">
                    <div className="absolute top-0 right-0 -mr-8 -mt-8 w-64 h-64 rounded-full bg-white/10 blur-3xl"></div>
                    <CardHeader>
                        <CardTitle className="text-white">Pro Tip</CardTitle>
                    </CardHeader>
                    <CardContent className="relative z-10">
                        <div className="space-y-4">
                            <h3 className="text-2xl font-bold">Maximize your revenue</h3>
                            <p className="text-white/80">
                                Check the "Orders" page frequently to process pending transactions and ensure customer satisfaction.
                            </p>
                            <div className="pt-4">
                                <button className="bg-white text-primary px-4 py-2 rounded-lg text-sm font-semibold hover:bg-slate-100 transition-colors">
                                    View Orders
                                </button>
                            </div>
                        </div>
                    </CardContent>
                </Card>
            </div>
        </div>
    );
}
